<template>
    <div>
      <a-tabs defaultActiveKey="1" @change="callback">
        <a-tab-pane tab="发布状况" key="1">
          <div class="title">条件搜索</div>
          <ul class="flex">
            <li class="flex">
                系统编号 
                <a-input placeholder="请输入机构代码" v-model="systemId1" style="width:150px ;margin-left:10px;"/>
            </li>
            <li class="flex">
                系统名称
                <a-select placeholder="请输入机构名称" v-model="systemChineseName1" style="width:150px ;margin-left:10px;" @change="systemDataChange1">
                  <a-select-option v-for="systemName in systemData1" :key="systemName.sysId" @click="systemDataClick1(systemName.chineseName)">{{systemName.chineseName}}</a-select-option>
                </a-select>
            </li>
            <li class="flex">
                服务类型
                <a-select placeholder="请输入服务类型" v-model="typename1" style="width:150px;margin-left:10px;" @change="ststemTypeChange1">
                  <a-select-option  :key="1">调用方</a-select-option>
                  <a-select-option  :key="0">提供方</a-select-option>
                </a-select>
            </li>
            <a-button icon="search"  style="margin-right:10px;" type="primary"  @click="inquire1">查询</a-button>
            <a-button icon="delete"  style="margin-right:10px;" type="primary"  @click="empty1">清空</a-button>

          </ul>   
          <div class="title" style="margin-top:20px;">操作视图</div>
          <div style="margin-bottom:20px">
              <a-button icon="upload"  style="margin-right:10px;">导出excel</a-button>
          </div>
          <div >
              <a-table :rowSelection="rowSelection" :columns="columns" :dataSource="data" >
                  <a slot="name" slot-scope="text" href="javascript:;">{{text}}</a>
              </a-table>
          </div>
        </a-tab-pane>
        <a-tab-pane tab="发布次数" key="2">
           <div class="title">条件搜索</div>
          <ul class="flex">
            <li class="flex">
                系统编号 
                <a-input placeholder="请输入机构代码" v-model="systemId2" style="width:150px ;margin-left:10px;"/>
            </li>
            <li class="flex">
                系统名称
                <a-select placeholder="请输入机构名称" v-model="systemChineseName2" style="width:150px ;margin-left:10px;" @change="systemDataChange2">
                  <a-select-option v-for="systemName in systemData2" :key="systemName.sysId" @click="systemDataClick2(systemName.chineseName)">{{systemName.chineseName}}</a-select-option>
                </a-select>
            </li>
            <li class="flex">
                服务类型
                <a-select placeholder="请输入服务类型" v-model="typename2" style="width:150px;margin-left:10px;" @change="ststemTypeChange2">
                  <a-select-option  :key="1">调用方</a-select-option>
                  <a-select-option  :key="0">提供方</a-select-option>
                </a-select>
            </li>
            <li class="flex">
              起始日期
              <a-date-picker @change="onChangeStart" placeholder="选择日期" style="width:150px;margin-left:10px;"/>
            </li>
            <li class="flex">
              结束日期
              <a-date-picker @change="onChangeEnd"   placeholder="选择日期" style="width:150px;margin-left:10px;"/>
            </li>
            <a-button icon="search"  style="margin-right:10px;" type="primary"  @click="inquire2">查询</a-button>
            <a-button icon="delete"  style="margin-right:10px;" type="primary"  @click="empty2">清空</a-button>

          </ul>   
          <div class="title" style="margin-top:20px;">操作视图</div>
          <div style="margin-bottom:20px">
              <a-button icon="upload"  style="margin-right:10px;">导出excel</a-button>
          </div>
          <div >
              <a-table :rowSelection="rowSelection" :columns="columns1" :dataSource="data1" >
                  <a slot="name" slot-scope="text" href="javascript:;">{{text}}</a>
              </a-table>
          </div>
        </a-tab-pane> 
      </a-tabs>
    </div>
</template>

<script>
const columns = [{
  title: '系统编码',
  dataIndex: 'systemId',
  scopedSlots: { customRender: 'systemId' },
}, {
  title: '系统名称',
  dataIndex: 'systemChineseName',
}, {
  title: '类型',
  dataIndex: 'type',
},{
  title: '关联服务数',
  dataIndex: 'serviceReleaseNum',
},{
  title: '已发布场景数',
  dataIndex: 'operationReleaseNum',
}];

const columns1 = [{
  title: '系统编码',
  dataIndex: 'systemId',
  scopedSlots: { customRender: 'systemId' },
}, {
  title: '系统名称',
  dataIndex: 'systemChineseName',
}, {
  title: '类型',
  dataIndex: 'type',
},{
  title: '关联服务数',
  dataIndex: 'serviceReleaseNum',
},{
  title: '发布场景次数',
  dataIndex: 'operationReleaseNum',
}];
export default {

  data() {
    return {
        data:[],//系统复用率
        data1:[],//服务复用率
        systemData1:[],//系统信息
        systemId1:"",//系统编号
        type1:"",//系统类型
        systemChineseName1:"",//系统名称
        cnname1:"",
        typename1:"",
        columns,
        columns1,
        systemData2:[],//系统信息
        systemId2:"",//系统编号
        type2:"",//系统类型
        systemChineseName2:"",//系统名称
        cnname2:"",
        typename2:"",
        startTime:"",//开始日期
        endTime:"",//结束日期
    }
  },
 computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        },
        getCheckboxProps: record => ({
          props: {
            disabled: record.name === 'Disabled User', // Column configuration not to be checked
            name: record.name,
          }
        }),
      }
    }
 },
 mounted(){
   //查询发布状态统计表
   this.releaseState(1,1,null,null,null);
   //查询全部信息
   this.getSystemAll();
  //  查询发布次数统计表
   this.releaseCount(1,1,"","","","","");
 },
  methods: {
    goTo(){
      this.$router.push({ path: "/platform" });
    },
    //查询全部信息
    getSystemAll () {
      let url = '/system/getSystemAlls'
      let params = {
        
      }
      let response = (res) => {
        let lists = res.data 
        lists.map((item, index) => {
          this.$set(item, 'key', index)
        })
        this.systemData1 = lists
        this.systemData2 = lists
      }
      this.$store.state.getMoreDataGet(url, params, response)
    },
    systemDataChange1(value) {
      this.systemId1 = value
    },
    ststemTypeChange1(value) {
      this.type1 = value
    },
    systemDataClick1(value){
      this.cnname1 = value
    },
    //搜索
    inquire1(){
       this.releaseState (1,1,this.systemId1,this.type1,this.cnname1) 
    },
    // 清空操作
    empty1 () {
      this.systemId1 = ""
      this.type1 = ""
      this.typename1 = ""
      this.systemChineseName1 = ""
      this.cnname1 = ""
    },

    systemDataChange2(value) {
      this.systemId2 = value
    },
    ststemTypeChange2(value) {
      this.type2 = value
    },
    systemDataClick2(value){
      this.cnname2 = value
    },
    //搜索
    inquire2(){
       this.releaseCount (1,1,this.systemId2,this.type2,this.cnname2,this.startTime,this.endTime) 
    },
    // 清空操作
    empty2 () {
      this.systemId2 = ""
      this.type2 = ""
      this.typename2 = ""
      this.systemChineseName2 = ""
      this.cnname2 = ""
      this.startTime = ""
      this.endTime = ""
    },

    //查询发布状态统计表
    releaseState (pageNo,rowNo,systemId,type,systemName) {
      let url = '/statistics/releaseState'
      let params = {
        pageNo: pageNo,
        rowNo: rowNo,
        systemId: systemId,
        type: type,
        systemName: systemName,
      }
      let response = (res) => {
        let lists = res.data.row
        lists.map((item, index) => {
          this.$set(item, 'key', index)
        })
        this.data = lists
      }
      this.$store.state.getMoreDataGet(url, params, response)
    },
    //查询发布次数统计表
    releaseCount (pageNo,rowNo,systemId,type,systemName,startDate,endDate) {
      let url = '/statistics/releaseCount'
      let params = {
        pageNo: pageNo,
        rowNo: rowNo,
        systemId: systemId,
        type: type,
        systemName: systemName,
        startDate:startDate,
        endDate:endDate
      }
      let response = (res) => {
        let lists = res.data.row
        lists.map((item, index) => {
          this.$set(item, 'key', index)
        })
        this.data1 = lists
      }
      this.$store.state.getMoreDataPost(url, params, response)
    },
    //开始日期
    onChangeStart(e){
      this.startTime = new Date(e).getFullYear()+"-"+Number(new Date(e).getMonth()+1)+"-"+new Date(e).getDate()
    },
    //结束日期
    onChangeEnd(e){
      this.endTime = new Date(e).getFullYear()+"-"+Number(new Date(e).getMonth()+1)+"-"+new Date(e).getDate()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
    font-size: 20px;
    font-weight: bold;
    margin-bottom:20px;
}
.flex{
    display: flex;
    display:-webkit-flex;
    align-items: center;
}
ul{
    list-style: none;
    padding:0;
}
li{
    margin-right: 10px;
}
#components-layout-demo-top-side-2 .logo {
  width: 120px;
  height: 31px;
  background: rgba(255,255,255,.2);
  margin: 16px 28px 16px 0;
  float: left;
}
.table-operations {
  margin-bottom: 16px;
}

.table-operations > button {
  margin-right: 8px;
}
.ant-tabs-tabpane-active{border: 0}
</style>



